:root {
  --primary: #ff6b35;
  --primary-dark: #e55a2b;
  --primary-light: #ff8c42;
  --secondary: #ffffff;
  --accent: #ffa952;
  --background: #0a101f;
  --foreground: #ffffff;
  --surface: #1a1a1a;
  --glass: rgba(255, 255, 255, 0.08);
  --gray-50: #f9fafb;
  --gray-100: #e5e7eb;
  --gray-200: #cbd5e1;
  --gray-300: #b0b0b0;
  --gray-600: #666666;
  --gray-700: #4a4a4a;
  --gray-800: #2a2a2a;
  --gray-900: #0f172a;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: radial-gradient(120% 120% at 20% 20%, rgba(255, 107, 53, 0.12), transparent),
    radial-gradient(100% 100% at 80% 0%, rgba(255, 169, 82, 0.12), transparent),
    var(--background);
  color: var(--foreground);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
}
.gradient-bg {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  min-height: 100vh;
}
.portal-card {
  background: var(--surface);
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.25);
  padding: 50px 60px;
  max-width: 450px;
  margin: 0 auto;
  color: var(--foreground);
  animation: slideInUp 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
}
.portal-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 30px 80px rgba(255, 107, 53, 0.3);
}
.dashboard-card {
  background: var(--surface);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  padding: 40px;
  max-width: 1400px;
  margin: 40px auto;
  color: var(--foreground);
  animation: fadeInScale 0.6s ease-out both;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.dashboard-card:hover {
  box-shadow: 0 15px 50px rgba(255, 107, 53, 0.25);
}
.form-input {
  width: 100%;
  padding: 14px 18px;
  border: 2px solid var(--gray-200);
  border-radius: 10px;
  font-size: 15px;
  background: var(--gray-900);
  color: var(--foreground);
  outline: none;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
}
.form-input:focus {
  border-color: #ff6b35;
  background: #0b1220;
  box-shadow: 0 0 0 4px rgba(255, 107, 53, 0.4), inset 0 0 10px rgba(255, 107, 53, 0.1);
  transform: translateY(-2px);
}
.btn-primary {
  background: linear-gradient(135deg, #ff6b35 0%, #ffa952 100%);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 14px 28px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 26px rgba(255, 107, 53, 0.35);
}
.btn-primary:hover {
  transform: translateY(-4px);
  box-shadow: 0 15px 40px rgba(255, 107, 53, 0.4);
  animation: buttonHoverGlow 1.5s ease-in-out infinite;
}
.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.btn-secondary {
  background: transparent;
  color: #ff6b35;
  border: 2px solid #ff6b35;
  border-radius: 10px;
  padding: 12px 26px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
}
.btn-secondary::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: #ff6b35;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
  z-index: -1;
}
.btn-secondary:hover {
  background: #ff6b35;
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(255, 107, 53, 0.3);
}
.btn-secondary:hover::before {
  width: 300px;
  height: 300px;
}
.btn-danger {
  background: linear-gradient(135deg, var(--secondary) 0%, #b91c1c 100%);
  color: #0f172a;
  border: none;
  border-radius: 10px;
  padding: 14px 28px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0 8px 20px rgba(185, 28, 28, 0.3);
}
.btn-danger:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(185, 28, 28, 0.5);
}
.error-text {
  color: var(--secondary);
  font-size: 14px;
  margin-top: 12px;
  text-align: center;
}
.card {
  background: var(--surface);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  padding: 24px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  color: var(--foreground);
  animation: slideInUp 0.6s ease-out both;
}
.card:hover {
  transform: translateY(-12px) scale(1.02);
  box-shadow: 0 25px 60px rgba(255, 107, 53, 0.25);
}
.section-title {
  font-size: 34px;
  font-weight: 700;
  color: #f8fafc;
  margin: 0 0 12px;
}
.section-subtitle {
  font-size: 17px;
  color: var(--gray-300);
  margin: 0;
  max-width: 760px;
}
.placeholder-section {
  height: 100px;
  border: 2px dashed var(--gray-300);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--gray-600);
  font-size: 16px;
  background: var(--gray-50);
}
.placeholder-section:last-child {
  margin-bottom: 0;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.animate-fade-in {
  animation: fadeIn 0.6s ease-out forwards;
}
.animate-slide-in {
  animation: slideIn 0.6s ease-out forwards;
}
.news-ticker {
  background: var(--primary);
  color: white;
  padding: 10px 0;
  overflow: hidden;
  white-space: nowrap;
}
.news-ticker-content {
  display: inline-block;
  animation: ticker 30s linear infinite;
}
@keyframes ticker {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.stats-item {
  text-align: center;
  padding: 30px 20px;
}
.stats-number {
  font-size: 48px;
  font-weight: 700;
  color: var(--primary);
  line-height: 1;
}
.stats-label {
  font-size: 16px;
  color: var(--gray-600);
  margin-top: 8px;
}
.nav-link {
  color: var(--gray-700);
  text-decoration: none;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 8px;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
}
.nav-link:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: var(--gray-100);
  z-index: -1;
  transition: left 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.nav-link:hover {
  background: var(--gray-100);
  color: var(--primary);
  transform: translateY(-2px);
}
.nav-link.active {
  background: var(--primary);
  color: white;
  box-shadow: 0 8px 20px rgba(255, 107, 53, 0.3);
}
@media (max-width: 768px) {
  .portal-card {
    padding: 30px 24px;
    margin: 20px;
  }
  .dashboard-card {
    margin: 20px;
    padding: 24px;
  }
  .section-title {
    font-size: 28px;
  }
}
.quick-links-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}
.quick-link-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  background: var(--surface);
  border-radius: 12px;
  box-shadow: 0 8px 26px rgba(0, 0, 0, 0.18);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  text-decoration: none;
  color: var(--foreground);
  position: relative;
  overflow: hidden;
}
.quick-link-item::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transform: rotate(45deg);
  animation: shimmer 3s infinite;
}
.quick-link-item:hover {
  transform: translateY(-12px) scale(1.05);
  box-shadow: 0 20px 50px rgba(255, 107, 53, 0.3);
}
.quick-link-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #ff6b35 0%, #ffa952 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  margin-bottom: 12px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0 8px 20px rgba(255, 107, 53, 0.3);
}
.quick-link-item:hover .quick-link-icon {
  transform: scale(1.15) rotate(10deg);
  box-shadow: 0 12px 30px rgba(255, 107, 53, 0.5);
}
.quick-link-text {
  font-weight: 700;
  color: #f8fafc;
  text-align: center;
}
.quick-link-desc {
  font-size: 13px;
  color: var(--gray-300);
  text-align: center;
  margin-top: 8px;
}
.page-root {
  background: radial-gradient(120% 120% at 20% 20%, rgba(255, 107, 53, 0.12), transparent),
    radial-gradient(100% 100% at 80% 0%, rgba(255, 169, 82, 0.12), transparent),
    var(--background);
}
.section-shell {
  max-width: 1200px;
  margin: 0 auto;
  padding: 80px 24px;
}
.section-head {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 24px;
  margin-bottom: 32px;
}
.eyebrow {
  font-size: 13px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #ff6b35;
  margin-bottom: 8px;
  font-weight: 700;
}
.section-title {
  font-size: 36px;
  font-weight: 800;
  color: #f8fafc;
  margin: 0 0 10px;
}
.section-subtitle {
  font-size: 16px;
  color: var(--gray-300);
  margin: 0;
  max-width: 720px;
}
.badge-inline {
  padding: 10px 14px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.04);
  color: var(--gray-200);
  font-size: 13px;
  white-space: nowrap;
}
.badge-row {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 16px;
}
.meta-badge {
  padding: 10px 14px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.08);
  color: var(--gray-100);
  font-weight: 600;
  font-size: 13px;
}
.pill {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 14px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--gray-100);
  font-size: 13px;
  font-weight: 700;
}
.hero {
  position: relative;
  min-height: 86vh;
  display: grid;
  grid-template-columns: 1.4fr 0.8fr;
  align-items: center;
  padding: 80px 24px;
  max-width: 1400px;
  margin: 0 auto;
  gap: 32px;
}
.hero-video-wrapper {
  position: absolute;
  inset: 0;
  overflow: hidden;
  border-radius: 0 0 32px 32px;
}
.hero-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.4) saturate(1.1);
}
.hero-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(120deg, rgba(10, 16, 31, 0.7) 0%, rgba(10, 16, 31, 0.4) 50%, rgba(10, 16, 31, 0.85) 100%);
  pointer-events: none;
}
.hero-content {
  position: relative;
  z-index: 1;
  max-width: 720px;
}
.hero-title {
  font-size: 42px;
  line-height: 1.2;
  color: #f8fafc;
  margin: 18px 0 14px;
  font-weight: 800;
}
.hero-subtitle {
  font-size: 18px;
  color: var(--gray-200);
  max-width: 620px;
  margin-bottom: 20px;
}
.hero-actions {
  display: flex;
  gap: 12px;
  align-items: center;
}
.hero-meta {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 18px;
}
.hero-sidecard {
  position: relative;
  z-index: 1;
  justify-self: end;
}
.glass-card {
  background: rgba(15, 23, 42, 0.75);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 18px;
  padding: 22px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.45);
  color: #e5e7eb;
}
.card-kicker {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--gray-300);
  margin-bottom: 10px;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 16px;
}
.card-stat {
  padding: 14px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.06);
}
.stat-number {
  font-size: 28px;
  font-weight: 800;
  color: #f8fafc;
}
.stat-label {
  font-size: 13px;
  color: var(--gray-300);
}
.card-foot {
  margin-top: 14px;
  font-size: 13px;
  color: var(--gray-300);
}
.rank-section {
  padding-top: 40px;
}
.rank-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 18px;
}
.rank-card {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.04) 0%, rgba(255, 255, 255, 0.02) 100%);
  border-radius: 16px;
  padding: 18px;
